.header-top{
  height:38px;
  width:1200px;
  margin: 0 auto;
  color: #212121;
  >ul{
    height:100%;
    >li{
      box-sizing: border-box;
      position: relative;
      padding: 0 25px;
      float: right;
      height: 100%;
      line-height: 38px;
      text-align: center;
      &:after{
        content: '';
        display: block;
        height: 15px;
        width: 1px;
        float: right;
        background-color: #cccccc;
        margin-top: 11.5px;
        margin-right: -25px;
      }
    }
    //关于我们
    >.focusus{
        >img{
          margin-left: 7px;
          cursor: pointer;
        }
        &:after{
          display: none;
        }
    }
    // app下载
    >.appdownload{

    }
    // 联系我们
    .contactus{

    }
    // 消息
    .message{

    }
    // 功能选项
    .functions{

    }
  }
}
